class TrackElement{
  constructor(path, title){
    this.url = path;
    this.title = title;
    this.element = new Audio(path);
    this.time = 0.00;
    this.setElementProperty = this.setElementProperty.bind(this);
    this.getElementProperty = this.getElementProperty.bind(this);
  }
  setElementProperty(propName, value){
    this.element[propName] = value;
  }
  getElementProperty(propName){
    return this.element[propName];
  }
}

class MediaContentContainer extends Component {
  constructor(props){
    super(props);
    this.state = {
      selectedTrack: null,
      audioElements: {},
      showPlay: false,
      tracks: null,
      muted: false,
      playing: false,
      volume: 0.5
    };
    this.handleSelection = this.handleSelection.bind(this);
    this.pauseTrack = this.pauseTrack.bind(this);
    this.playTrack = this.playTrack.bind(this);
    this.toggleMute = this.toggleMute.bind(this);
    this.createElements = this.createElements.bind(this);
    this.getSelectedTrack = this.getSelectedTrack.bind(this);
  }
  handleSelection(trackIdx){
    const track = this.state.tracks[trackIdx].element;
    this.pauseAllTracks();
    track.play();
    this.setState({selectedTrack: trackIdx, playing: true});
  }

  createElements(){
    let elements = [];
    let tracks = this.props.media.tracks;

    for(var ti in tracks){
      const track = tracks[ti];
      elements.push(new TrackElement(track.path, track.title));
    }

    this.setState({tracks: elements});

  }

  getSelectedTrack(){
    return this.state.tracks[this.state.selectedTrack];
  }

  toggleMute(){
    const element = this.getSelectedTrack();
    const vol = this.state.muted ? 0 : this.state.volume;
    this.state.muted = !this.state.muted;
    element.setElementProperty("volume", vol);
  }

  pauseTrack(){
    const element = this.state.tracks[this.state.selectedTrack].element;
    element.pause();
    this.setState({showPlay: true, playing: false});
  }

  playTrack(){
    const element = this.state.tracks[this.state.selectedTrack].element;
    element.play();
    this.setState({showPlay: false, playing: true});
  }

  pauseAllTracks(){
    for(var ti in this.state.tracks){
        const track = this.state.tracks[ti].element;
        track.pause();
    }
  }

  componentDidMount(){
    this.createElements();
  }

  render(){
    var element = null;
    var track = null;
    if(this.state.tracks !== null && this.state.selectedTrack !== null){
      element = this.state.tracks[this.state.selectedTrack].element;
      track = this.state.tracks[this.state.selectedTrack];
    }
    return(
      <div className="MediaContentContainer">
        <div className="Selector">
          <img src={this.props.media.cover} />
          <div className="MediaContent">
            <div className="ProjectTitle">
              {this.props.media.projectName}
            </div>
            <div className="overflow-y-auto">
              {this.props.media.tracks.map((track, index) =>
                <MediaSelector
                track={track}
                currentTrack={track}
                handleSelection={this.handleSelection}
                idx={index}/>
              )}
            </div>
          </div>
        </div>

        {this.state.selectedTrack &&
          <MediaController
          currentElement={element}
          currentTrack={track}
          showPlay={this.state.showPlay}
          track={this.props.media.tracks[this.state.selectedTrack]}
          playing={this.state.playing}
          onPlay={this.playTrack}
          onPause={this.pauseTrack}
          onMute={this.toggleMute}
          />
        }
      </div>
    )
  }
}

class MediaController extends Component {
  constructor(props){
    super(props)
    this.state = {trackPos: 0};
    this.onSeek = this.onSeek.bind(this);
  }
  onSeek(ev){
    const val = ev.target.value;
    const track = this.props.currentTrack;
    track.setElementProperty("currentTime", val);
    this.setState({trackPos: val});
  }
  render(){
    const duration = this.props.currentElement.duration
    const mclass = this.props.muted ? <span>M</span> : <span>UM</span>
    return(
      <div className="Controller">
        <span>{this.props.track.title}</span>
        <span onClick={this.props.onMute}>
          { mclass }
        </span>
        <input type="range" onChange={this.onSeek} min={0} max={duration} value={this.state.trackPos} className="SeekControl flex-1" />
        <PlayPause showPlay={this.props.showPlay} playTrack={this.props.onPlay} pauseTrack={this.props.onPause} />
      </div>
    )
  }
}

function MediaSelector(props) {
  let duration = null;
  return(
    <div className="MediaSelector" onClick={() => props.handleSelection(props.idx)}>
      <span>{props.track.title}</span>
      <div className="flex-1" />
      <span className="padding-right-30">{duration}</span>
    </div>
  )
}

function PlayPause(props) {
  const showPlay = props.showPlay;
  const callback = showPlay ? props.playTrack : props.pauseTrack;
  return(
    <div className="ControlButton">
      { showPlay ? <span onClick={() => props.playTrack()}>pl</span> : <span onClick={() => props.pauseTrack() }>pa</span> }
    </div>
  )
}

